/* You can add global styles to this file, and also import other style files */
* {
  box-sizing: border-box;
  margin: 0;
  outline: 0;
  font-family: 'Poppins', sans-serif;
}
html {
  width: 100vw;
  height: 100vh;
}
body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
p, div {
      color: #171717;
}
:root {
  --primary-color: #002949;
//  --primary-color: #78cc6d;
  --app-border-radius: 4px;
  --app-box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05);
  --app-inner-card-width: 620px;
  --app-inner-card-height: 570px;
  --app-profile-width: 440px;
}

.border-line-v {
  &:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 100%;
    background: -moz-radial-gradient(top, ellipse cover, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
    background: -webkit-radial-gradient(top, ellipse cover, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
    background: radial-gradient(ellipse at top, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
  }
}

.border-line-h {
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: -moz-radial-gradient(left, ellipse cover, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
    background: -webkit-radial-gradient(left, ellipse cover, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
    background: radial-gradient(ellipse at left, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
  }
}


.inner-card-wrapper {
  width: var(--app-inner-card-width);
  height: var(--app-inner-card-height);
  box-shadow: var(--app-box-shadow);
  border-radius: 0 var(--app-border-radius) var(--app-border-radius) 0;
  background: #ffffff;
  margin-top: 15px;
}

.inner-card {
  width: var(--app-inner-card-width);
  height: var(--app-inner-card-height);
  padding: 30px 30px 30px 30px;
  overflow-y: auto;
  .inner-card__content {
    color: #000;
    .inner-card__content__title {
      margin: 0 0 15px 0;
      padding: 0 0 5px 0;
      position: relative;
      font-size: 17px;
      color: #171717;
      line-height: 21px;
      font-weight: 500;
        &::after {
          content: '';
          position: absolute;
          left: -30px;
          bottom: -15px;
          width: 95%;
          height: 1px;
          background: radial-gradient(ellipse at left, rgba(197, 202, 213, 0.7) 0%, rgba(255, 255, 255, 0) 70%);
        }
    }
    .inner-card__content__text {
      position: relative;
      padding: 20px;
    }
  }
}

.info-list {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  .info-box {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    width: 50%;
    .info-box__title {
      font-size: 13px;
      color: #171717;
      font-weight: bold;
      opacity: 0.85;
      margin-right: 10px;
      text-transform: uppercase;
    }
    .info-box__content {
      font-size: 13px;
    }
  }
}


.ps {
  .ps__rail-y {
    background: transparent;
  }
}

@media (max-width: 1199px) {
  :root {
    --app-inner-card-width: 455px;
//  --app-inner-card-height: 570px;
  }
}

@media (max-width: 992px) {
  :root {
    --app-inner-card-width: 406px;
//  --app-inner-card-height: 570px;
  }
  .inner-card {
    height: auto;
  }
  .inner-card-wrapper {
    height: auto;
  }
}

@media (max-width: 768px) {
  html {
    font-size: 12px;
  }
  :root {
    --app-profile-width: 440px;
  }
}

@media (max-width: 576px) {
  :root {
    --app-profile-width: 90vw;
    --app-inner-card-width: 350px;
  }
  .info-list {
    flex-direction: column;
  }

}

#starfield {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
}

#smoke-effect {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
 // background-image: linear-gradient(to bottom right, #002949 0%, #5489b6 100%);
}
